header {
  border-bottom: thick double #479bf1;
  display: flex;
  width: 100%;
  border-radius: 0 0 20px 20px;
  background-color: #303036;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99;
  color: whitesmoke;
}

.main-menu {
  display: flex;
  width: 80%;
  margin: auto;
}

.logo {
  height: 50px;
  margin: 0;
  border: none;
}

.menu {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.menu-item {
  margin-left: 20px;
}

.burger-checkbox {
  visibility: hidden;
}

.menu-list {
  list-style-type: none;
  display: flex;
}

.soc-btns {
  display: none;
}

@media (max-width: 700px) {
  .soc-btns {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 20px;
  }

  .burger-checkbox {
    position: absolute;
  }
  .burger {
    position: relative;
    z-index: 1;
    cursor: pointer;
    display: block;
    position: relative;
    border: none;
    background: transparent;
    width: 40px;
    height: 26px;
  }
  .burger::before,
  .burger::after {
    content: "";
    left: 0;
    position: absolute;
    display: block;
    width: 100%;
    height: 4px;
    border-radius: 10px;
    background: #479bf1;
  }
  .burger::before {
    top: 0;
    box-shadow: 0 11px 0 #479bf1;
    transition: box-shadow 0.3s 0.15s, top 0.3s 0.15s, transform 0.3s;
  }
  .burger::after {
    bottom: 0;
    transition: bottom 0.3s 0.15s, transform 0.3s;
  }
  .burger-checkbox:checked + .burger::before {
    top: 11px;
    transform: rotate(45deg);
    box-shadow: 0 6px 0 rgba(0, 0, 0, 0);
    transition: box-shadow 0.15s, top 0.3s, transform 0.3s 0.15s;
  }
  .burger-checkbox:checked + .burger::after {
    bottom: 11px;
    transform: rotate(-45deg);
    transition: bottom 0.3s, transform 0.3s 0.15s;
  }
  .menu-list {
    top: 0;
    right: 0;
    position: absolute;
    gap: 12px;
    padding: 70px 0;
    margin: 0;
    background: #303036;
    list-style-type: none;
    transform: translateX(+200%);
    transition: 0.3s;
    width: 50%;
    border-left: thick double #479bf1;
    border-bottom: thick double #479bf1;
    border-radius: 15px 0 15px 15px;
    display: block;
  }
  .menu-item {
    text-align: center;
    display: block;
    padding: 10% 0 10%;
    color: white;
    font-size: 130%;
    text-decoration: none;
    margin-left: 0;
  }
  .menu-item:hover {
    background: rgba(255, 255, 255, 0.2);
  }
  .burger-checkbox:checked ~ .menu-list {
    transform: translateX(0);
  }
}
